.head {
    font-size: 2rem;
    font-weight: bold;
    margin-left: 1rem;
}

.cont {
    border: 1rem solid #473a9f;
    border-radius: 5rem;
    width: 28rem;
    margin: 2rem;
    padding: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    box-shadow: 9px 10px #888;
    background: #b0cefc;
}

.cont img {
    align-self: center;
    height: 9rem;
    width: 14rem;
    margin: 1rem;
    border-radius: 1rem;
}

.cont h3 {
    font-weight: bold;
    /* color: rgb(255, 115, 0); */
}

.maincon {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}